

























import Vue from "vue";

import { DayData } from '../CrowTypes'

export default Vue.extend({
    name: 'DailyLine',
    props: {
        days: {
            type: Array as () => DayData[],
            default: []
        } ,
        height: {
            type: Number,
            default: 100
        },
        color: {
            type: String,
            default: "rgb(33, 37, 41)"
        }
    },
    computed: {
      lineStyle: function(): Record<string, unknown> {
        return {
          'stroke-width': 1,
          'pointer-events': 'none',
          'stroke': this.color
        }
      },
      textStyle: function(): Record<string, unknown> {
        return {
          'font': '12px sans-serif',
          'fill': this.color
        }
      }
    }
});
